.typesPage {
    min-height: 90vh;
    min-width: 100vw;
    background-color: #75cae4;
}
.pokemonTypesWrapper {
    min-height: 90vh;
    padding-top: 120px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
}
.pokemonTypeText {
    text-align: center;
    font-size: 30px;
    font-weight: 100;
    color: white;
}
.pokemonTypeIcon {
    cursor: pointer;
    width: 170px;
}

@media (max-width: 768px) {
    .pokemonTypeIcon {
        width: 120px;
    }
}

@media (max-width: 576px) {
    .pokemonTypeIcon {
        width: 70px;
    }
    .pokemonTypeText {
        font-size: 22px;
    }
}